import "core-js/modules/es.array.push.js";
import { mapGetters } from 'vuex';
export default {
  data() {
    return {
      settings: {
        config: []
      },
      // 加载状态
      loading: false
    };
  },
  computed: {
    ...mapGetters(['credentials'])
  },
  methods: {
    async fetchWithAuth(url, options = {}) {
      // 开发环境, url 前面加上 /api
      // url = `/api${url}`;
      if (this.credentials) {
        // 设置 Authorization 头
        options.headers = {
          ...options.headers,
          'Authorization': `Basic ${this.credentials}`
        };
        // 确保包含凭据，如 cookies
        options.credentials = 'include';
      }
      const response = await fetch(url, options);
      if (response.status === 401) {
        // Redirect to the login page if a 401 Unauthorized is returned
        this.$message.error('认证状态错误，请重新登录');
        this.$router.push('/adminLogin');
        throw new Error('Unauthorized');
      }
      return response;
    },
    saveSettings() {
      this.fetchWithAuth('/api/manage/sysConfig/page', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(this.settings)
      }).then(() => this.$message.success('设置已保存'));
    },
    // 根据设置标签返回相应的表情符号
    getInputEmoji(label) {
      const emojiMap = {
        '网站标题': '📝',
        '网站描述': '📄',
        '网站关键词': '🔍',
        '网站图标': '🔖',
        '页脚链接': '🔗',
        '所有者名称': '👤',
        '禁用页脚': '👣',
        '自定义CSS': '🎭',
        '自定义JS': '⚙️'
      };

      // 默认表情
      return emojiMap[label] || '🌈';
    }
  },
  mounted() {
    this.loading = true;
    // 获取上传设置
    this.fetchWithAuth('/api/manage/sysConfig/page').then(response => response.json()).then(data => {
      this.settings = data;
    }).finally(() => {
      this.loading = false;
    });
  }
};